@charset 'UTF-8';

@media screen and (min-width: 980px) {
    .slide-wrap {
        padding-top: 20px;

        .pd-title {
            padding-left: 0;
            margin-bottom: 30px;
            color: #000;
            font-size: 28px;
            font-weight: 400;

            .big-font {
                font-size: 30px;
            }
        }
    }

    .more_prod_slide {
        margin-left: -12px;
        margin-right: -12px;

        .slick-list {
            padding: 0;
        }

        .slick-slide {
            margin: 0 12px;
        }
    }
    
    .mps-info { 
        height: 380px;

        .mps-cate {
            padding: 10px 0;
            font-size: 14px;
        }

        .mps-title {
            a {
                line-height: 18px;
                height: 34px;
                margin-bottom: 39px;
                font-size: 14px;
            }
        }

        .mps-sale {
            bottom: 70px;
            margin-bottom: 8px;
            font-size: 14px;
        }

        .mps-pay {
            margin-bottom: 0;
            font-size: 22px;
        }
    }

    .cate_main {
        .plc_fc-pro,
        .plc_mo-fc {
            display: none;
        }

        .pd-title {
            display: block;
        }
    }
}

@media screen and ( max-width: 979px ) {
    .sz-custom_label,
    .sz-custom_text {
        cursor: inherit;
    }

    .product_list-box {
        margin-left: 0;
    }

    .product-item {
        width: 50%;
        min-height: 422px;
        padding-left: 0;
    }

    .product-item {
        height: auto;
        padding-bottom: 40px;

        .pli-thumb {
            margin-bottom: 15px;
        }

        .pli-tag {
            .pli-tag-item {
                line-height: 20px;
                font-size: 10px;
                font-weight: 400;
            }
        }

        .pli-coupon {
            font-weight: 400;

            .big-font {
                font-weight: 300;
            }
        }

        .pli-cate {
            font-size: 12px;
        }

        .pli-title {
            line-height: 17px;
            font-size: 12px;
            margin-bottom: 10px;
        }

        .pli_pay-info {
            margin-bottom: 5px;
            font-size: 12px;

            .pli-per {
                font-size: 16px;
            }

            .pli-pay {
                font-size: 16px;
            }

            .pli-sale {
                font-size: 10px;
            }
        }
        .pli-info {
            padding: 0 15px;
        }

        .option_info-box {
            margin: 0 15px;
        }

        .pli_like-box {
            margin: 15px 15px 0 15px;
            padding-bottom: 15px;
            font-size: 12px;

            .big-font {
                font-size: 12px;
            }

            .pli-like {
                margin-right: 6px;

                &::before {
                    width: 13px;
                    height: 12px;
                    background: url(../images/icon/heart-icon_mo.png) center center / cover no-repeat;
                    vertical-align: -2px;
                }
            }

            .pli-favo {
                .object {
                    margin-left: 0;
                }

                &::before {
                    width: 13px;
                    height: 12px;
                    background: url(../images/icon/star-trans-icon_mo.png) center center / cover no-repeat;
                    vertical-align: -2px;
                }
            }
        }
    }
    
    .option-view .ov-title::after {
        width: 10px;
        height: 11px;
        background: url(../images/icon/acco_arrow-gray_mo.png) center center/ cover no-repeat;
        vertical-align: -1px;
    }

    .option-detail .ov-arrow {
        margin-top: 8px;
        width: 10px;
        height: 11px;
        background: url(../images/icon/acco_arrow-gray_mo.png) center center/ cover no-repeat;
    }

    .option-view {
        line-height: 30px;
    }

    .option-detail {
        top: 30px;
        line-height: 28px;
        font-size: 10px;

        .option-title {
            padding: 0 8px;
        }
    }

    .option-acco.depth-3 .option-title {
        padding: 0 8px 0 15px;
    }

    .plc_fc-box {
        padding: 20px 15px 9px 15px;
        margin-bottom: 0;

        &.active {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            padding: 19px 15px 18px;
            background-color: #fff;
            z-index: 10;

            .plc_fc-pro {
                display: none;
            }
        }

        &.search_active {
            position: fixed;
            top: 55px;
            left: 0;
            width: 100%;
            background-color: #fff;
        }

        .sz-custom_label {
            width: 16px;
            height: 16px;
        }
        
        .sz-custom_text {
            color: #333;
            margin-left: 2px;
            margin-right: 12px;
            vertical-align: 4px;
        }
    }

    .plc_fc-filter {
        margin-top: -2px;

        .filter_view {
            font-size: 12px;

            &::after {
                width: 11px;
                height: 12px;
                background: url(../images/icon/filter-icon_mo.png) center center / cover no-repeat;
            }
        }

        .filter_select {
            font-size: 12px;

            &::before {
                height: 12px;
            }

            &::after {
                width: 11px;
                height: 12px;
                background: url(../images/icon/view-icon_mo.png) center center / cover no-repeat;
            }

            &.active::after {
                background: url(../images/icon/view_active-icon_mo.png) center center / cover no-repeat;
            }
        }
    }

    .plc_fc-pro {
        display: block;
        margin-top: 15px;
        font-size: 11px;

        span {
            font-size: 12px;
        }
    }

    .plc_mo-fc {
        display: block;
    }

    .product_list-mo_nav {
        display: block;
    }

    .product_list-wrap.padding-top {
        padding-top: 0;
    }

    .cate_main-slide {
        .cate_main-item {
            height: 245px;

            .cmi-text {
                left: 30px;
                width: 178px;

                .cmi-title {
                    line-height: 30px;
                    font-size: 26px;

                    .big-font {
                        font-size: 30px
                    }
                }
            }

            .cmi-sub {
                font-size: 14px;

                .big-font {
                    font-size: 15px;
                }
            }
        }

        .slick-arrow {
            display: none !important;
        }
    }

    .cate_main-slide-wrap {
        margin-bottom: 0;

        .cate_main-nav span {
            bottom: 15px;
            left: auto;
            right: 15px;
            width: 45px;
            height: 18px;
            line-height: 19px;
            font-size: 11px;
            transform: none;
        }
    }

    .pd_slide-nav {
        display: none;
    }

    .more_prod_slide {
        .slick-arrow {
            display: none !important;
        }
    }

    .product_list-content {
        .slide-wrap {
            overflow: hidden;
        }

        .more_prod_slide .slick-list {
            padding: 0 90px 0 15px;
        }
    }

    .plb_title-box {
        width: 100%;
        padding: 20px 15px 9px 15px;

        .just_left,
        .just_right {
            width: 50%;
        }

        .just_right {
            text-align: right;
        }

        .plc_fc-filter {
            padding-top: 0;
        }

        .plc_fc-pro {
            margin-top: 11px;
        }

        .sz-custom_label {
            width: 16px;
            height: 16px;
        }

        .sz-custom_text {
            color: #333;
            margin-left: 2px;
            margin-right: 12px;
            vertical-align: 4px;
        }

        &.active {
            width: 100%;
            padding: 19px 15px 18px;
            transform: none;

            .plc_fc-pro {
                display: none;
            }
        }
    }

    .product-item {
        .soldout {
            font-size: 23px;
        }
    }

    .slide-wrap {
        &.mo_border {
            padding-bottom: 5px;
            border-bottom: 8px solid #f0f0f0;
        }
    }

    .pl_tag-box {
        padding: 0 15px;
        margin-top: 40px;

        &.mo-only {
            display: block;
        }

        .plt-title {
            margin-bottom: 12px;
            font-size: 18px;
        }
    }

    .pl_tag-list {
        li {
            padding-bottom: 0;
            padding-top: 8px;
        }

        .pl_tag-item {
            height: 36px;
            padding: 0 15px;
            color: #555;
            font-size: 15px;
            border-radius: 30px;
        }
    }
}

@media screen and (min-width: 1556px) {
    .product_list-nav {
        display: block;
    }

    .page-header {
        display: none;
    }
    
    .product_list-wrap {
        max-width: 1536px;
        padding: 0 10px;
    }

    .product_list-content {
        max-width: 1260px;
    }

    .product_list-box {
        margin-left: -20px;
    }

    .product-item {
        width: 20%;
        padding-left: 20px;
    }
}

@media screen and (min-width: 980px) and (max-width: 1556px) {
    .product_list-nav {
        display: block;
    }

    .page-header {
        display: none;
    }

    .product_list-wrap {
        max-width: 1280px;
        padding: 0 10px;
    }

    .plb_title-box.active {
        width: 1004px;
    }

    .product_list-content {
        max-width: 1004px;
    }

    .product_list-box {
        margin-left: -20px;
    }

    .product-item {
        width: 25%;
        padding-left: 20px;
    }

    .product_list-content {
        .more_prod_slide {
            .slick-list {
                padding-right: 158px;
            }
        }
    
        .more_prod_slide .slick-next {
            right: -10px;
        }
    }
}

@media screen and (min-width: 1150px) and (max-width: 1279px) {
    .product_list-wrap {
        max-width: 1150px;
        padding: 0 10px;
    }

    .plb_title-box.active {
        width: 875px;
        transform: translateX(19px);
    }

    .product_list-content {
        max-width: calc(100% - 256px);
    }

    .product_list-box {
        margin-left: -20px;
    }

    .product-item {
        width: 33.33%;
        padding-left: 20px;
    }

    .product_list-content {
        .mps-info .mps-sale {
            bottom: 36px;
        }
    }

    .more_prod_slide {
        .slick-arrow {
            top: 96px;
        }
    }
}

@media screen and (min-width: 980px) and (max-width: 1149px) {
    .product_list-wrap {
        max-width: 980px;
        padding: 0 10px;
    }

    .plb_title-box.active {
        width: 705px;
        transform: translateX(19px);
    }

    .product_list-content {
        max-width: calc(100% - 256px);
    }

    .product_list-box {
        margin-left: -20px;
    }

    .product-item {
        width: 50%;
        height: auto;
        padding-left: 20px;
    }

    .product_list-content {
        .mps-info {
            height: auto;
        }
    
        .mps-info .mps-sale {
            bottom: 18px;
        }
    }

    .more_prod_slide {
        .slick-arrow {
            top: 115px;
        }
    }
}